import React, { useEffect, useState } from "react";
import { Dropdown, Menu } from "semantic-ui-react";
import avatar from "./../images/IMG_7404.jpg";

export default function Navbar() {
  const handleOnclick = (e, { name }) => {
    setPointing(name);

    const item = e.target.innerText;
    let scrollItem = document.getElementById(item);
    e.preventDefault();
    scrollItem &&
      scrollItem.scrollIntoView({ behavior: "smooth", block: "start" });
    // window.history.pushState(item, item, `/${item}`);
    console.log(item);
  };

  const handleOnclick2 = (e) => {
    window.scroll({
      top: 0,
      left: 0,
      behavior: "smooth"
    });
  };

  const projectOptions = [
    {
      text: "FullstackProjects",
      value: "FullStackProjects",
      onClick: handleOnclick
    },
    { text: "Websites", value: "Websites", onClick: handleOnclick },

    { text: "Python-Based", value: "Python-Based", onClick: handleOnclick }
  ];

  useEffect(() => {
    let url = window.location.href.split("/");
    let target = url[url.length - 1].toLowerCase();
    let element = document.getElementById(target);
    element && element.scrollIntoView({ behavior: "smooth", block: "start" });
  }, []);

  const [pointing, setPointing] = useState("Home");

  return (
    <Menu className="navbar" pointing stackable secondary size="massive">
      <Menu.Item
        name="About"
        active={pointing === "About"}
        onClick={handleOnclick2}
      />
      <Dropdown
        className="active item"
        placeholder="Projects"
        options={projectOptions}
      />
      <Menu.Item
        name="Contact"
        active={pointing === "Contact"}
        onClick={handleOnclick}
      />
      <Menu.Item
        position="right"
        name="About"
        className="abbyavatar"
        id="About"
        onClick={handleOnclick2}
      >
        <img
          src={avatar}
          style={{
            borderRadius: "50%",
            maxWidth: "70%",
            height: "auto",
            paddingRight: "5px"
          }}
        />
        Abby
      </Menu.Item>
    </Menu>
  );
}
